import React from 'react';
import Button from '../Button/Button'
import classes from "./WinWindow.module.css";

const phrases = (percent) => {
  if (percent < 10) {
    return 'Народжаны поўзаць лётаць не можа'
  } else if (percent < 50) {
    return 'Вам яшчэ трэба паслухаць спевы птушак'
  } else if (percent < 80) {
    return 'Трэба яшчэ раз паспрабаваць, амаль атрымалася'
  } else {
    return 'А ці не птушка вы?'
  }
}

const WinWindow = props => {

  return (
    <div className={classes.WinWindow}>
      <div className={classes.ModalWindow}>
        <div className={classes.ModalWindow_Wrapper}>
          <div>
            <p>{phrases(props.score / 30 * 100)}</p>
            <hr />
            <p>{`Вы набралі "${props.score}" з "30" балаў`}</p>
          </div>
          <Button onClick={props.onClickButton}>Згуляць зноў?</Button>
        </div>
      </div>
    </div>
  )
}

export default WinWindow;
