import React from 'react';
import classes from './Question.module.css';
import Img from "../../components/Img/img"
import Audio from "../../components/Audio/Audio";
import emptyBerd from "../../assets/images/emptybird.jpg";

const Question = props => {
  const { win, hidden } = props;
  return (
    <div className={classes.Question}>
      <div>
        <Img src={win ? props.data.img : emptyBerd} />
      </div>
      <div className={classes.block_discription}>
        <p>{win ? props.data.Name : '?????????????????'}</p>
        <hr hidden={!hidden} />
        <div hidden={hidden}>
          <hr />
          <p>{!hidden ? props.data.NameLat : ''}</p>
          <hr />
        </div>
        <Audio song={props.data.song} hash={props.hash} win={win} />
      </div>
    </div>
  )
}

export default Question;